@font-face {
  font-family: Quicksand;
  font-weight: bold;
  src: url("../../fonts/src/Quicksand-Bold/Quicksand-Bold.eot");
  src: url("../../fonts/src/Quicksand-Bold/Quicksand-Bold.woff") format("woff"), url("../../fonts/src/Quicksand-Bold/Quicksand-Bold.ttf") format("truetype"), url("../../fonts/src/Quicksand-Bold/Quicksand-Bold.svg") format("svg");
}
@font-face {
  font-family: Quicksand;
  font-weight: normal;
  src: url("../../fonts/src/Quicksand-Normal/Quicksand-Normal.eot");
  src: url("../../fonts/src/Quicksand-Normal/Quicksand-Normal.woff") format("woff"), url("../../fonts/src/Quicksand-Normal/Quicksand-Normal.ttf") format("truetype"), url("../../fonts/src/Quicksand-Normal/Quicksand-Normal.svg") format("svg");
}
@font-face {
  font-family: Montserrat;
  font-weight: normal;
  src: url("../../fonts/src/Montserrat-Normal/Montserrat-Normal.eot");
  src: url("../../fonts/src/Montserrat-Normal/Montserrat-Normal.woff") format("woff"), url("../../fonts/src/Montserrat-Normal/Montserrat-Normal.ttf") format("truetype"), url("../../fonts/src/Montserrat-Normal/Montserrat-Normal.svg") format("svg");
}
@font-face {
  font-family: Montserrat;
  font-weight: bold;
  src: url("../../fonts/src/Montserrat-Bold/Montserrat-Bold.eot");
  src: url("../../fonts/src/Montserrat-Bold/Montserrat-Bold.woff") format("woff"), url("../../fonts/src/Montserrat-Bold/Montserrat-Bold.ttf") format("truetype"), url("../../fonts/src/Montserrat-Bold/Montserrat-Bold.svg") format("svg");
}
.palette-item {
  margin-bottom: 20px;
}

.palette-wrapper {
  display: flex;
  column-gap: 20px;
}

.palette-plate {
  border-radius: 6px;
  width: 70px;
  height: 70px;
  margin-right: 40px;
}

.color-header {
  font-family: Quicksand, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;
  color: #1f2041;
  margin-top: 9px;
}

.color-code {
  font-family: Quicksand, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 24px;
  color: #1f2041;
  margin-top: 6px;
  font-size: 18px;
}

.description-wrapper {
  display: flex;
  flex-direction: column;
}

.darkShade100 {
  background-color: #1f2041;
}

.darkShade75 {
  background-color: rgba(31, 32, 65, 0.75);
}

.darkShade50 {
  background-color: rgba(31, 32, 65, 0.5);
}

.darkShade25 {
  background-color: rgba(31, 32, 65, 0.25);
}

.darkShade5 {
  background-color: rgba(31, 32, 65, 0.05);
}

.purple {
  background-color: #bc9cff;
}

.green {
  background-color: #6fcf97;
}

.palette-list {
  list-style: none;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.logo-header {
  padding: 30px;
}

.fonts-item {
  padding-bottom: 50px;
}

.fonts-wrapper {
  display: grid;
  grid-template-columns: 1fr 4fr;
}

.font-label {
  color: rgba(31, 32, 65, 0.25);
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  text-align: right;
  margin-right: 30px;
}

.H1__text {
  font-family: Quicksand, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  color: #1f2041;
}

.H2__text {
  font-family: Quicksand, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;
  color: #1f2041;
}

.H3__text {
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  /* identical to box height */
  text-transform: uppercase;
  color: #1f2041;
}

.body__text {
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
}

.fonts-list {
  list-style: none;
  padding-right: calc(100% - 403px);
}

.container {
  margin: 0 auto;
}

.colors-type-main {
  display: grid;
  column-gap: calc(100% - 886px);
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  padding-left: 150px;
  padding-right: 90px;
  padding-top: 33px;
  row-gap: 50px;
}